import axios from 'axios'
import {Toast} from "vant";
let wx = {};
wx.request = (params) => {
    axios({method: params.method, url: params.url, data: params.data}).then(res => {
        if(res.message) {
            Toast('页面开小差，请重新导诊');
        }
        params.success(res)
    }).catch(err => {
        Toast('页面开小差，请重新导诊');
        params.fail(err);
    });
}
export default wx;
